import React from "react";
import CustomModal from "../../components/Modal/customModal";
import ReusableAlert from "../../components/Alert/ReusableAlert";

const ConfirmationModal = ({ show, onHide, onConfirm }) => {
  return (
    <CustomModal
      show={show}
      onHide={onHide}
      title="Confirm Submission"
      footerButtons={[
        {
          label: "Cancel",
          variant: "danger",
          onClick: onHide,
        },
        {
          label: "Submit",
          variant: "success",
          onClick: onConfirm,
        },
      ]}
    >
      <div className="confirmation-modal-content" style={{ padding: "10px 0" }}>
        <h4
          style={{ fontWeight: "600", marginBottom: "15px", color: "#e70909" }}
        >
          Are you sure you want to submit? This action cannot be undone.
        </h4>

        <ReusableAlert
          variant="info"
          className="mb-3"
          message={
            <span
              style={{ display: "flex", alignItems: "center", gap: "10px" }}
            >
              <span
                style={{
                  fontSize: "14px",
                  backgroundColor: "#bee5eb",
                  color: "#000",
                  padding: "3px 6px",
                  borderRadius: "50%",
                  fontWeight: "bold",
                }}
              >
                i
              </span>

              <span style={{ fontSize: "15px", lineHeight: "1.5" }}>
                Once submitted, your data will be processed by the system. You
                will receive a confirmation once its complete. Large uploads may
                take a few minutes.
              </span>
            </span>
          }
        />
      </div>
    </CustomModal>
  );
};

export default ConfirmationModal;
