import React, { useMemo, useState, useEffect } from "react";
import CustomModal from "../Modal/customModal";
import {
  AutoSizer,
  List,
  CellMeasurer,
  CellMeasurerCache,
} from "react-virtualized";
import "react-virtualized/styles.css";
import "./SuccessModal.css";
import ReusableAlert from "../Alert/ReusableAlert";
import { handleExportExcel } from "../../utils/helpers";
import CustomButton from "../Button/Button";
import Pagination from "../Pagination/Pagination";

const SuccessModal = ({
  show = false,
  onHide = () => {},
  responseData = [],
}) => {
  const itemsPerPage = 15;
  const [currentPage, setCurrentPage] = useState(1);

  // Memoized CellMeasurer cache
  const cache = useMemo(() => {
    return new CellMeasurerCache({
      fixedWidth: true,
      defaultHeight: 50,
    });
  }, []);

  // Paginate response data
  const paginatedData = useMemo(() => {
    const start = (currentPage - 1) * itemsPerPage;
    return responseData.slice(start, start + itemsPerPage);
  }, [currentPage, responseData]);

  const handleExport = () => {
    handleExportExcel(responseData, "upload-results.xlsx");
  };

  console.log("SuccessModal received response data:", responseData);

  useEffect(() => {
    cache.clearAll();
  }, [paginatedData, cache]);

  const handlePageChange = (newPage) => {
    setCurrentPage(newPage);
  };

  const columns = [
    { key: "EIN Number", header: "EIN Number", width: "25%" },
    { key: "revision", header: "Revision", width: "10%" },
    { key: "status", header: "Status", width: "15%" },
    { key: "message", header: "Message", width: "50%" },
  ];

  const rowRenderer = ({ key, index, style, parent }) => {
    const item = paginatedData[index];
    if (!item) return null;

    return (
      <CellMeasurer
        key={key}
        cache={cache}
        columnIndex={0}
        rowIndex={index}
        parent={parent}
      >
        <div style={style} className="virtualized-row">
          <div
            className="virtualized-cell"
            style={{ width: columns[0].width }}
            title={item.name}
          >
            {item.name}
          </div>
          <div
            className="virtualized-cell"
            style={{ width: columns[1].width }}
            title={item.revision}
          >
            {item.revision}
          </div>
          <div
            className={`virtualized-cell status-cell ${item.status.toLowerCase()}`}
            style={{ width: columns[2].width }}
            title={item.status}
          >
            {item.status}
          </div>
          <div
            className="virtualized-cell"
            style={{ width: columns[3].width }}
            title={item.message}
          >
            {item.message}
          </div>
        </div>
      </CellMeasurer>
    );
  };

  return (
    <CustomModal
      show={show}
      onHide={onHide}
      title="Upload Results"
      size="lg"
      footerButtons={[
        {
          label: "Close",
          variant: "danger",
          onClick: onHide,
        },
      ]}
    >
      <div className="success-modal-content">
        <div className="top-bar">
          <div></div>
          <CustomButton
            variant="success"
            size="lg"
            onClick={handleExport}
            className="m-2 border-bottom-10px"
            text="Export to Excel "
          />
        </div>

        <ReusableAlert
          variant="success"
          message={
            <p className="success-message-text">
               <strong>Success:</strong> {responseData.length} item
              {responseData.length > 1 ? "s" : ""} processed.
            </p>
          }
          className="mb-3"
        />

        <div className="virtualized-table-container">
          <div className="virtualized-header">
            {columns.map((column) => (
              <div
                key={column.key}
                className="virtualized-header-cell"
                style={{ width: column.width }}
              >
                {column.header}
              </div>
            ))}
          </div>

          <div className="virtualized-body">
            <AutoSizer>
              {({ height, width }) => (
                <List
                  width={width}
                  height={height - 10}
                  rowCount={paginatedData.length}
                  rowHeight={cache.rowHeight}
                  deferredMeasurementCache={cache}
                  rowRenderer={rowRenderer}
                  overscanRowCount={5}
                />
              )}
            </AutoSizer>
          </div>
        </div>

        {/* Pagination Controls */}
        <Pagination
          currentPage={currentPage}
          totalItems={responseData.length}
          itemsPerPage={itemsPerPage}
          onPageChange={handlePageChange}
        />
      </div>
    </CustomModal>
  );
};

export default SuccessModal;
